.App {
  text-align: center;
}
.btn-danger {
  margin: 5px;
}

.circle:hover {
  fill: green;
}
.container {
  border: solid #34495e 1px;
  height: 800px;
  width: 800px;
  margin: 0;
  padding: 0;
  overflow: scroll;
}
.content {
  background-color: #eefbff;
  width: 60vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.coords-list {
  display: flex;
  flex-direction: column;
  color: white;
  overflow-y: scroll;
  max-height: 350px;
  list-style: none;
  padding: 0;
  margin-top: 5px;
}

.coords-list > li {
  margin: 4px;
  text-align: center;
  width: 350px;
}

.coords-text {
  display: inline-block;
  width: 130px;
}

.elements-item {
  width: 350px;
  text-align: center;
  margin-bottom: 10px;
}

.elements-list {
  display: flex;
  flex-direction: column;
  color: white;
  margin: 5px;
  list-style: none;
  overflow-y: scroll;
  max-height: 350px;
}

.flexbox-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
}

input {
  width: 50px;
}

.myform {
  text-align: center;
}

.sidebar-flexbox-container {
  height: 100vh;
  width: 550px;
  display: flex;
  background-color: #34495e;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

/* #svg {
  width: 800px;
  height: 500px;
} */
text {
  font-size: 25px;
}

ul {
  padding: 0;
}
